.horizontal__display {
  display: flex;
  flex-direction: row;
}

.horizontal__display img {
  object-fit: cover;
  height: 250px;
  width: 250px;
}

.vertical__display img {
  width: 100%;
  max-width: 150px;
}

.filter__button,
.filter__button--selected,
.filter__button--reset {
  padding: 8px 16px 8px 16px;
  margin: 8px 8px 0 8px;
  width: fit-content;
  border: none;
  font-weight: 550;
  color: black;
  background-color: white;
  border: 1px solid black;
}

.filter__button--selected,
.filter__button--reset {
  background-color: black;
  color: white;
}

.filter__button:hover {
  cursor: pointer;
}

.review__teaser--headline {
  font-weight: 600;
}
