.App {
  text-align: center;
}

.app__logo {
  margin-bottom: 0;
  font-size: 64px;
  padding-top: 32px;
}

.preview__grid {
  display: flex;
  margin: 0 auto;
  width: 70%;
}

.preview__grid--image-wrapper {
  flex-wrap: wrap;
  display: flex;
  justify-content: left;
}

.footer__links ul {
  display: flex;
  flex-wrap: wrap;

  justify-content: center;
  list-style-type: none;
  margin: 0;
  padding: 0;
  color: black;
}

.footer__links {
  position: fixed;
  padding: 10px;
  bottom: 0;
  left: 0;
  right: 0;
}

.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: calc(10px + 2vmin);
  margin: 2em 0;
  width: 100%;
  max-width: 1200px;
}