.review__teaser {
  display: flex;
  justify-content: center;
  margin: 2em;
  text-decoration: none;
}

.review__teaser--content {
  display: flex;
  margin: 1em;
  max-width: 100%;
}

.review__teaser--content--img {
  max-width: 200px;
  max-height: 350px;
}

.review__teaser--link {
  color: black;
  text-decoration: underline;
  width: "fit-content";
}

.review__teaser--dice {
  font-size: 24px;
}


@media only screen and (max-width: 600px) {
  .review__teaser--content--img {
    max-width: 200px;
    max-height: 350px;
  }
}